import React, {Component} from 'react';
import classNames from 'classnames';
import TodoTextInput from './TodoTextInput';
import styles from '../index.scss';
// let cx = classNames.bind(styles);

class TodoItem extends Component {

    constructor(props) {
        super(props);
        this.state = {
            editing: false
        };
    }

    /**
     * editingtruesetState
     */
    handleDoubleClick(){
        this.setState({editing: true})
    }

    /**
     * bind(this)
     * 0 
     */
    handleSave(id, text){
        if (text.length === 0) {
            this.props.deleteTodo(id);
        } else {
            this.props.editTodo(id, text);
        }
        this.setState({editing: false});
    }

    render() {
        const {todo, completeTodo, deleteTodo} = this.props;
        /**
         * ediTodoTextInputtodoitem
         */
        let element;
        if (this.state.editing) {
            element = (
                <TodoTextInput text={todo.text}
                               editing={this.state.editing}
                               onSave={(text) => this.handleSave(todo.id, text)}/>
            )
        } else {
            element = (
                <div className={styles.view}>
                    <input className={styles.toggle}
                           type="checkbox"
                           checked={todo.completed}
                           onChange={() => completeTodo(todo.id)}/>
                    <label onDoubleClick={this.handleDoubleClick.bind(this)}>
                        {todo.text}
                    </label>
                    <button className={styles.destroy}
                            onClick={() => deleteTodo(todo.id)}/>
                </div>
            )
        }

        let completedClass = todo.completed ? 'completed' : '';
        let editingClass = this.state.editing ? 'editing' : '';
        // cx({
        //     completed: todo.completed,
        //     editing: this.state.editing
        // })
        return (
            <li className={`${styles[completedClass]} ${styles[editingClass]}`}>
                {element}
            </li>
        )
    }
}

TodoItem.propTypes = {
    todo: React.PropTypes.object.isRequired,       // todoitemtext / id / completed
    editTodo: React.PropTypes.func.isRequired,     // 
    deleteTodo: React.PropTypes.func.isRequired,   // 
    completeTodo: React.PropTypes.func.isRequired  // todoitem
};

export default  TodoItem;


