import React, {Component} from 'react';
import classNames from 'classnames';
import {SHOW_ALL, SHOW_COMPLETED, SHOW_ACTIVE} from '../constants/TodoFilters';
import styles from '../index.scss';
// let cx = classNames.bind(styles);

/* []*/
const FILTER_TITLES = {
    [SHOW_ALL]: 'All',
    [SHOW_ACTIVE]: 'Active',
    [SHOW_COMPLETED]: 'Completed'
};

class Footer extends Component {

    /**
     * actietodoitem
     * itemstodoitemNo
     */
    renderTodoCount() {
        const {activeCount} = this.props;
        const itemWord = activeCount === 1 ? 'item' : 'items';

        return (
            <span className={styles['todo-count']}>
                <strong>{activeCount || 'No'}</strong> {itemWord} left
            </span>
        )
    }

    /**
     * todolistall / active / completed
     */
    renderFilterLink(filter) {
        const title = FILTER_TITLES[filter];
        /* selectedFilterfilterfilter selectedFilter = this.props.filter */
        const {filter: selectedFilter, onShow} = this.props;

        let selectedClass = filter === selectedFilter ? 'selected' : '';
        // cx({selected: filter === selectedFilter})
        return (
            <a className={styles[selectedClass]}
               style={{cursor: 'pointer'}}
               onClick={() => onShow(filter)}>
                {title}
            </a>
        )
    }

    /**
     * todoitemtodoitem
     */
    renderClearButton() {
        const {completedCount, onClearCompleted} = this.props;
        if (completedCount > 0) {
            return (
                <button className={styles['clear-completed']}
                        onClick={onClearCompleted}>
                    Clear completed
                </button>
            )
        }
    }

    render() {
        return (
            <footer className={styles.footer}>
                {this.renderTodoCount()}
                <ul className={styles.filters}>
                    {[SHOW_ALL, SHOW_ACTIVE, SHOW_COMPLETED].map(filter =>
                        <li key={filter}>
                            {this.renderFilterLink(filter)}
                        </li>
                    )}
                </ul>
                {this.renderClearButton()}
            </footer>
        )
    }
}

Footer.propTypes = {
    completedCount: React.PropTypes.number.isRequired,    // todoitem
    activeCount: React.PropTypes.number.isRequired,       // todoitem
    filter: React.PropTypes.string.isRequired,            // todolist all / active / completed
    onClearCompleted: React.PropTypes.func.isRequired,    // 
    onShow: React.PropTypes.func.isRequired               //  all / active / completed filters
};

export default Footer;


