import React, { PropTypes } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import Header from './components/Header';
import MainSection from './components/MainSection';
import * as TodoActions from './actions';
import styles from './index.scss';

const App = ({todos, actions}) => (
    <div className={`page ${styles.color}`}>
        <div className={styles.container}>
            <Header addTodo={actions.addTodo}/>
            <MainSection todos={todos} actions={actions}/>
        </div>
    </div>
);

App.propTypes = {
  todos: PropTypes.array.isRequired,
  actions: PropTypes.object.isRequired
};

/* todo, todoitem */
const mapStateToProps = state => ({
  todos: state.todos
});

/**
 *  bindActionCreators  action creator 
 *  Redux  Redux store  dispatch 
 *
 * 
 * 1. actionCreators (Function or Object):  action creator action creators 
 * 2. dispatch (Function):  dispatch  Store 
 *
 * 
 * (Function or Object): 
 *  dispatch action actionCreators
 *
 *  bindActionCreatorsthis.props.dispatch(actions.addTodo())
 *   this.props.actions.addTodo(), dispatch
 *  reduxapi
 */
const mapDispatchToProps = dispatch => ({
    actions: bindActionCreators(TodoActions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App)


