import React, {Component} from 'react';
import classNames from 'classnames';
import styles from '../index.scss';
// let cx = classNames.bind(styles);

class TodoTextInput extends Component {

    constructor(props) {
        super(props);
        // this.props.text todoitem
        this.state = {
            text: this.props.text || ''
        };
    }

    /**
     * onKeyDown, 
     * todoform,onSubmitinput
     */
    handleSubmit(e) {
        const text = e.target.value.trim();  //if
        if (e.which === 13) {
            this.props.onSave(text);
            if (this.props.newTodo) {
                this.setState({text: ''});
            }
        }
    }

    /**
     * 
     */
    handleChange(e) {
        this.setState({text: e.target.value});
    }

    /**
     * 1. 
     * 2. todoitem, 
     */
    handleBlur(e) {
        if (!this.props.newTodo) {
            this.props.onSave(e.target.value);
        }
    }

    render() {

        let editClass = this.props.editing ? 'edit' : '';
        let newTodoClass = this.props.newTodo ? 'new-todo' : '';
        // const inputClass =  cx({
        //     edit: this.props.editing,
        //     'new-todo': this.props.newTodo
        // });
        return (
            <input className={`${styles[editClass]} ${styles[newTodoClass]}`}
                   type="text"
                   placeholder={this.props.placeholder}
                   autoFocus="true"
                   value={this.state.text}
                   onBlur={this.handleBlur.bind(this)}
                   onChange={this.handleChange.bind(this)}
                   onKeyDown={this.handleSubmit.bind(this)}/>
        )
    }
}

TodoTextInput.propTypes = {
    onSave: React.PropTypes.func.isRequired,   // todoitem
    text: React.PropTypes.string,              // todoiteminput value
    placeholder: React.PropTypes.string,       // 
    editing: React.PropTypes.bool,             // 
    newTodo: React.PropTypes.bool              // header
};

export default TodoTextInput;




